.technical-inspection {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  overflow-y: auto;
  overflow-x: hidden;
}

.field__med {
  width: 270px;
}

.tech_file img {
  width: 280px;
  border-radius: 20px;
}

.tech-title {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: 300;
  font-size: 28px;
  line-height: 33px;
  text-align: center;
  color: #005665;
  margin: 0 0 20px 0;
}

.MuiFormControlLabel-root {
  width: 300px;
  margin: 0 0 20px 0;
}

.MuiIconButton-root {
  border-radius: 10px;
}

.MuiButtonBase-root {
  background-color: #FFFFFF;
}

.MuiSvgIcon-root {
  fill: #263337;
}

.MuiTypography-body1 {
  margin: 0 0 0 20px;
}